.skills-container {
    scroll-snap-align: center;
    letter-spacing: 4px;
    position: relative;
    width: 100%;
    margin: 0 auto;
    padding: 10%;
    height: 100vh;
    background-color: #182525;
    color: white;
}
.skills-heading {
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 5%;
}
.skills-title {
    text-align: left;
    margin-left: 5%;
}
.skills-text {
    text-align: left;
    margin-left: 20%;
}
